<template>
  <div class="twitter-link-form">
    <div v-if="loading" class="loading">
      <i class="fas fa-spinner fa-spin"></i> Checking Twitter status...
    </div>
    <div v-else>
      <div v-if="isLinked" class="twitter-linked-container">
        <div class="success-message">
          <i class="fas fa-check-circle"></i> 
          Your Twitter account <strong>@{{ twitterUsername }}</strong> is linked!
        </div>
        <button @click="unlinkTwitterAccount" class="unlink-button" :disabled="unlinking">
          <i class="fas fa-unlink" v-if="!unlinking"></i>
          <i class="fas fa-spinner fa-spin" v-else></i>
          {{ unlinking ? 'Unlinking...' : 'Unlink Twitter Account' }}
        </button>
      </div>
      <form v-else @submit.prevent="linkTwitterAccount" class="link-form">
        <div class="form-group">
          <label for="twitterUsername">Twitter Username <small>(case sensitive)</small></label>
          <div class="input-wrapper">
            <span class="input-prefix">@</span>
            <input 
              type="text" 
              id="twitterUsername" 
              v-model="twitterUsername" 
              placeholder="Enter your Twitter username"
              required
            />
          </div>
          <button type="submit" class="submit-button" :disabled="linking">
            <i class="fas fa-link" v-if="!linking"></i>
            <i class="fas fa-spinner fa-spin" v-else></i>
            {{ linking ? 'Linking...' : 'Link Twitter Account' }}
          </button>
        </div>
        <div v-if="error" class="error-message form-error">
          <i class="fas fa-exclamation-circle"></i> {{ error }}
        </div>
        
        
        <div class="form-group" v-if="false">
          <label for="verificationCode">Verification Tweet</label>
          <div class="verification-instructions">
            <p>To verify your Twitter account, please tweet the following text:</p>
            <div class="verification-code">
              <code>Verifying my account for BITCAT: {{ verificationCode }}</code>
              <button type="button" @click="copyVerificationCode" class="copy-button">
                <i class="fas fa-copy"></i>
              </button>
            </div>
            <a 
              :href="tweetUrl" 
              target="_blank" 
              rel="noopener noreferrer" 
              class="tweet-button"
            >
              <i class="fab fa-twitter"></i> Tweet Now
            </a>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';

const store = useStore();
const userTag = computed(() => store.state.user?.userTag);

const twitterUsername = ref('');
const verificationCode = ref('');
const isLinked = ref(false);
const loading = ref(true);
const linking = ref(false);
const unlinking = ref(false);
const error = ref('');

// Событие для уведомления родительского компонента об изменении статуса привязки
const emit = defineEmits(['status-changed']);

// Генерация кода верификации
const generateVerificationCode = () => {
  const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let result = '';
  for (let i = 0; i < 10; i++) {
    result += characters.charAt(Math.floor(Math.random() * characters.length));
  }
  return result;
};

// URL для твита
const tweetUrl = computed(() => {
  if (!verificationCode.value) return '';
  const text = `Verifying my account for BITCAT: ${verificationCode.value}`;
  return `https://x.com/intent/tweet?text=${encodeURIComponent(text)}`;
});

// Копирование кода верификации
const copyVerificationCode = () => {
  const text = `Verifying my account for BITCAT: ${verificationCode.value}`;
  navigator.clipboard.writeText(text);
};

// Проверка статуса привязки Twitter
const checkTwitterStatus = async () => {
  try {
    loading.value = true;
    error.value = '';
    
    const response = await axios.get('/twitter/check-linked-account');
    
    if (response.data.success) {
      isLinked.value = response.data.isLinked;
      if (isLinked.value) {
        twitterUsername.value = response.data.twitterUsername;
      }
    } else {
      error.value = response.data.message || 'Error checking Twitter status';
    }
  } catch (err) {
    console.error('Error checking Twitter status:', err);
    error.value = 'Error checking Twitter status. Please try again.';
  } finally {
    loading.value = false;
  }
};

// Привязка Twitter-аккаунта
const linkTwitterAccount = async () => {
  if (!twitterUsername.value) return;
  
  linking.value = true;
  error.value = '';
  
  try {
    const response = await axios.post('/twitter/add-username', {
      twitterUsername: twitterUsername.value
      // verificationCode: verificationCode.value
    });
    
    if (response.data.success) {
      isLinked.value = true;
      error.value = '';
      emit('status-changed', true);
    } else {
      error.value = response.data.message || 'Failed to link Twitter account';
    }
  } catch (err) {
    console.error('Error linking Twitter account:', err);
    error.value = err.response?.data?.message || 'Error linking Twitter account';
  } finally {
    linking.value = false;
  }
};

// Отвязка Twitter-аккаунта
const unlinkTwitterAccount = async () => {
  try {
    unlinking.value = true;
    error.value = '';
    
    const response = await axios.post('/twitter/unlink-account');
    
    if (response.data.success) {
      isLinked.value = false;
      twitterUsername.value = '';
      // Уведомляем родительский компонент об изменении статуса
      emit('status-changed', false);
    } else {
      error.value = response.data.message || 'Error unlinking Twitter account';
    }
  } catch (err) {
    console.error('Error unlinking Twitter account:', err);
    error.value = err.response?.data?.message || 'Error unlinking Twitter account. Please try again.';
  } finally {
    unlinking.value = false;
  }
};

onMounted(() => {
  // verificationCode.value = generateVerificationCode();
  checkTwitterStatus();
});
</script>

