<template>
  <div class="popup-overlay" v-if="show" @click.self="closePopup">
    <div class="popup-content">
      <div class="popup-header">
        <h2>Convert Power to Rewards</h2>
        <button class="close-button" @click="closePopup">×</button>
      </div>
      
      <Toast 
        :show="showToast" 
        :message="toastMessage" 
        :type="toastType" 
        @close="showToast = false" 
      />
      
      <div class="popup-body">
        <div class="power-info">
          <div class="power-balance">
            <i class="fas fa-bolt"></i>
            <span>Your Power: <strong>{{ userPower.power }}</strong></span>
          </div>
        </div>
        
        <div class="conversion-options">
          <div class="conversion-option">
            <div class="option-header">
              <i class="fas fa-ticket-alt"></i>
              <h3>Free NFT Mint <span v-if="userPower.power < conversionRates.freeMintCost" class="not-enough-power">Not enough</span></h3>
            </div>
            <div class="option-details">
              <p>Current balance: <strong>{{ userPower.freeMint }}</strong></p>
              <p>Cost: <strong>{{ conversionRates.freeMintCost }} Power</strong></p>
              <button 
                class="convert-button" 
                :disabled="userPower.power < conversionRates.freeMintCost || isLoading"
                @click="convertPower('freeMint')"
              >
                <span v-if="!isLoading">Convert to Free NFT Mint</span>
                <span v-else><i class="fas fa-spinner fa-spin"></i> Converting...</span>
              </button>
            </div>
          </div>
          
          <div class="conversion-option">
            <div class="option-header">
              <i class="fas fa-rocket"></i>
              <h3>Free NFT Launch <span v-if="userPower.power < conversionRates.freeMintCost" class="not-enough-power">Not enough</span></h3>
            </div>
            <div class="option-details">
              <p>Current balance: <strong>{{ userPower.freeNftLaunch }}</strong></p>
              <p>Cost: <strong>{{ conversionRates.freeNftLaunchCost }} Power</strong></p>
              <button 
                class="convert-button" 
                :disabled="userPower.power < conversionRates.freeNftLaunchCost || isLoading"
                @click="convertPower('freeNftLaunch')"
              >
                <span v-if="!isLoading">Convert to Free NFT Launch</span>
                <span v-else><i class="fas fa-spinner fa-spin"></i> Converting...</span>
              </button>
            </div>
          </div>
          
          <div class="conversion-option disabled">
            <div class="option-header">
              <i class="fas fa-coins"></i>
              <h3>Free Token Launch <span class="coming-soon">Coming Soon</span></h3>
            </div>
            <div class="option-details">
              <p>Current balance: <strong>{{ userPower.freeTokenLaunch }}</strong></p>
              <p>Cost: <strong>{{ conversionRates.freeTokenLaunchCost }} Power</strong></p>
              <button class="convert-button" disabled>
                Convert to Free Token Launch
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted, watch } from 'vue';
import axios from 'axios';
import Toast from '@/components/Toast.vue';

export default {
  name: 'PowerConvertPopup',
  components: {
    Toast
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    userPowerData: {
      type: Object,
      required: true
    }
  },
  emits: ['close', 'conversion-complete'],
  setup(props, { emit }) {
    const showToast = ref(false);
    const toastMessage = ref('');
    const toastType = ref('info');
    const isLoading = ref(false);
    const userPower = reactive({
      power: 0,
      freeMint: 0,
      freeNftLaunch: 0,
      freeTokenLaunch: 0
    });
    
    const conversionRates = reactive({
      freeMintCost: '-',
      freeNftLaunchCost: '-',
      freeTokenLaunchCost: '-'
    });
    
    // Функция для показа уведомлений
    const showToastMessage = (message, type = 'info') => {
      toastMessage.value = message;
      toastType.value = type;
      showToast.value = true;
    };
    
    // Update userPower when props change
    const updateUserPower = () => {
      if (props.userPowerData) {
        userPower.power = props.userPowerData.power || 0;
        userPower.freeMint = props.userPowerData.freeMint || 0;
        userPower.freeNftLaunch = props.userPowerData.freeNftLaunch || 0;
        userPower.freeTokenLaunch = props.userPowerData.freeTokenLaunch || 0;
      }
    };
    
    // Watch for changes in props
    watch(() => props.userPowerData, updateUserPower, { immediate: true, deep: true });
    
    // Fetch conversion rates from backend
    const fetchConversionRates = async () => {
      try {
        const response = await axios.get('/rewards/conversion-rates');
        if (response.data.success) {
          conversionRates.freeMintCost = response.data.rates.freeMintCost;
          conversionRates.freeNftLaunchCost = response.data.rates.freeNftLaunchCost;
          conversionRates.freeTokenLaunchCost = response.data.rates.freeTokenLaunchCost;
        }
      } catch (error) {
        console.error('Error fetching conversion rates:', error);
        showToastMessage('Failed to load conversion rates', 'error');
      }
    };
    
    // Convert power to selected reward type
    const convertPower = async (rewardType) => {
      if (isLoading.value) return;
      
      const costMap = {
        freeMint: conversionRates.freeMintCost,
        freeNftLaunch: conversionRates.freeNftLaunchCost,
        freeTokenLaunch: conversionRates.freeTokenLaunchCost
      };
      
      const cost = costMap[rewardType];
      
      if (userPower.power < cost) {
        showToastMessage(`Not enough power. You need ${cost} power to convert.`, 'error');
        return;
      }
      
      isLoading.value = true;
      
      try {
        const response = await axios.post('/rewards/convert-power', {
          rewardType,
          amount: 1 // Convert 1 at a time
        });
        
        if (response.data.success) {
          showToastMessage(response.data.message || 'Power converted successfully!', 'success');
          emit('conversion-complete');
        } else {
          showToastMessage(response.data.message || 'Failed to convert power', 'error');
        }
      } catch (error) {
        console.error('Error converting power:', error);
        showToastMessage(error.response?.data?.message || 'Failed to convert power', 'error');
      } finally {
        isLoading.value = false;
      }
    };
    
    const closePopup = () => {
      emit('close');
    };
    
    onMounted(() => {
      fetchConversionRates();
      updateUserPower();
    });
    
    return {
      userPower,
      conversionRates,
      isLoading,
      convertPower,
      closePopup,
      showToast,
      toastMessage,
      toastType
    };
  }
}
</script>

