            </div>
          </div>
        </template>
        
        <!-- Telegram секция -->
        <template v-else-if="isAuthenticated && activeNetwork === 'telegram'">
          <!-- Секция для Telegram с использованием компонента TelegramLinkForm -->
          <div class="telegram-section">
            <div v-if="!isTelegramLinked" class="telegram-header">
              <i class="fab fa-telegram telegram-icon"></i>
              <h2>Link Your Telegram Account</h2>
