              <p>Just provide your username</p>
            </div>
            <TelegramLinkForm @status-changed="handleTelegramStatusChange" />
          </div>
        </template>
        
        <!-- Info секция -->
        <template v-else-if="activeNetwork === 'info'">
          <div class="info-section">
            <div class="info-card">
