                  <p class="info-block-text">Second place recive 5$ and 2 NFTs. Third place receive 1 NFT.</p>
                  <p class="highlight-text"><strong>Most active Telegram users participate in a separate draw with similar prizes every 7 days.</strong></p>
                  <p>In the future, when the budget allows, we will add <strong>more cash prizes..</strong></p>
                  <p>The funds will be credited to your linked wallet<small>(or type in BIO)</small>.</p>
                </div>
                
                <!-- BITCAT Power -->
                <div class="info-block">
                  <div class="info-block-header">
                    <i class="fas fa-bolt"></i>
                    <h3>BITCAT Power & Points</h3>
                  </div>
                  <p><strong>Power & Points</strong> are awarded simultaneously, but have different purposes.</p>
                  <p><strong>Power</strong> can be exchanged for coupons that will allow you to get paid functionality for free.</p>
                  <p><strong>Points</strong> are reset every week and are used to track your activity and determine the winner of the week.</p>
                  
                  
                  <div class="reward-list">
                    <h4>Twitter Rewards:</h4>
                    <ul class="no-bullets">
                      <li><i class="fas fa-heart"></i> <strong>Likes:</strong> +1 power for like</li>
