                      <li><i class="fas fa-retweet"></i> <strong>Retweets:</strong> +3 power for retweet</li>
                      <li><i class="fas fa-vote-yea"></i> <strong>Voting:</strong> +1 power for vote on posts</li>
                    </ul>
                  </div>

                  <p>For your social media activities, you earn BITCAT Power & Points.</p>
                </div>
                
                <!-- Посты -->
                <div class="info-block">
                  <div class="info-block-header">
                    <i class="fas fa-edit"></i>
                    <h3>Creating Posts</h3>
                  </div>
                  <p class="info-block-text">Users with NFTs (owned or rented) can add posts every 30 minutes for a reward:</p>
                  <ul>
                    <li><strong>+10 power</strong> for each non-blocked post</li>
                    <li>Each post is active for approximately <strong>1 hour</strong></li>
                  </ul>
                </div>
                
                <!-- Активные дни -->
                <div class="info-block">
                  <div class="info-block-header">
                    <i class="fas fa-calendar-check"></i>
                    <h3>Active Days Bonus</h3>
                  </div>
                  <p class="info-block-text">Earn bonus power for consecutive active days:</p>
                  <ul>
