                  <p>After 5 consecutive days, the counter resets.</p>
                  <p>For Twitter and Telegram 'Active Days' is separate.</p>
                </div>
                
                <!-- Телеграмм активность -->
                <div class="info-block">
                  <div class="info-block-header">
                    <i class="fab fa-telegram"></i>
                    <h3>Telegram Activity</h3>
                  </div>
                  <p class="info-block-text">Active Telegram users earn additional rewards:</p>
                  <ul>
                    <li><strong>+50 power</strong> for each day with 10+ messages (minimum 10 characters each)</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </template>

        <!-- Секция со списком всех постов (для всех пользователей) -->
        <div class="active-posts-section">
          <div class="posts-header">
            <h2>Reward {{ activeTab === 'history' ? 'History' : 'Posts' }}</h2>
            <div class="posts-tabs">
              <button 
                @click="switchTab('all')" 
                class="tab-button" 
                :class="{ 'active': activeTab === 'all' }"
              >
                <i class="fas fa-list"></i> All Posts
              </button>
              <button 
                @click="switchTab('active')" 
                class="tab-button" 
                :class="{ 'active': activeTab === 'active' }"
              >
                <i class="fas fa-check-circle"></i> Active
              </button>
              <button 
                @click="switchTab('history')" 
                class="tab-button" 
                :class="{ 'active': activeTab === 'history' }"
              >
                <i class="fas fa-history"></i> History
              </button>
            </div>
          </div>
          <!-- Индикатор загрузки при переключении табов -->
          <div v-if="tabSwitching" class="tab-loading-container">
            <div class="tab-loading-spinner">
              <i class="fas fa-gift fa-spin"></i>
            </div>
            <p>Loading content...</p>
          </div>

          <!-- Секция истории наград -->
          <div v-else-if="activeTab === 'history'" class="rewards-content min-height-content">
            <div v-if="loadingHistory" class="loading-spinner">
              <i class="fas fa-spinner fa-spin"></i> Loading rewards history...
            </div>
            <div v-else-if="rewardHistory.length === 0" class="no-history">
              <i class="fas fa-info-circle"></i>
              <p>No rewards history yet. Start earning rewards!</p>
            </div>
            <div v-else class="rewards-history-list">
              <div v-for="(item, index) in rewardHistory" :key="index" class="history-item">
                <div class="history-item-content">
                  <i class="fas fa-gift history-icon"></i>
                  <span>{{ item }}</span>
                </div>
              </div>
            </div>
          </div>

          <!-- Секция постов -->
          <div v-else class="min-height-content">
            <div v-if="loading" class="loading-spinner">
              <i class="fas fa-spinner fa-spin"></i> Loading posts...
            </div>
            <div v-else-if="activePosts.length === 0" class="no-posts">
              <p>No reward posts available. Add a post to start earning rewards!</p>
            </div>
            <div v-else class="posts-list">
              <RewardPostCard 
                v-for="post in activePosts" 
                :key="post.id" 
                :post="post" 
                @vote="handleVote"
                @end="handleEndPost"
              />
              
              <!-- Пагинация -->
              <div class="pagination">
                <button 
                  @click="changePage(currentPage - 1)" 
                  :disabled="currentPage <= 1"
                  class="pagination-button"
                >
                  <i class="fas fa-chevron-left"></i> Previous
                </button>
                
                <span class="pagination-info">Page {{ currentPage }} of {{ totalPages }}</span>
                
                <button 
                  @click="changePage(currentPage + 1)" 
                  :disabled="currentPage >= totalPages"
                  class="pagination-button"
                >
                  Next <i class="fas fa-chevron-right"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Toast 
        :message="toastMessage" 
        :type="toastType" 
        :show="showToast" 
        @close="closeToast"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, computed, watch } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import AddRewardPost from '@/components/rewards/AddRewardPost.vue';
import RewardPostCard from '@/components/rewards/RewardPostCard.vue';
import TwitterLinkForm from '@/components/twitter/TwitterLinkForm.vue';
import TelegramLinkForm from '@/components/telegram/TelegramLinkForm.vue';
import PowerConvertPopup from '@/components/rewards/PowerConvertPopup.vue';
import Toast from '@/components/Toast.vue';

const store = useStore();
const isAuthenticated = computed(() => store.getters.isAuthenticated);
const userTag = computed(() => store.state.user?.userTag);

// Состояние для popup конвертации Power
const showConvertPopup = ref(false);

// Состояние для управления активной сетью (Twitter/Telegram)
const activeNetwork = ref(localStorage.getItem('rewardsActiveNetwork') || 'twitter'); // По умолчанию активен Twitter

// Состояние для Telegram
const isTelegramLinked = ref(false);
const telegramUsername = ref('');
const telegramLinking = ref(false);
const telegramUnlinking = ref(false);
const telegramStats = reactive({
  totalMessages: 0,
  todayMessages: 0,
  weeklyMessages: 0,
  activeDays: 0
});

const activePosts = ref([]);
const userStatus = ref({
  isTwitterLinked: false,
  twitterUsername: '',
  canAddPosts: false,
  userPower: {
    power: 0,
    points: 0,
    freeMint: 0,
    freeNftLaunch: 0,
    freeTokenLaunch: 0,
    allLikes: 0,
    allRetweets: 0,
    allReplies: 0,
    activeDays: 0
  }
});
const loading = ref(true);
const currentPage = ref(1);
const totalPages = ref(1);
const totalPosts = ref(0);
const postsPerPage = ref(20);
const activeTab = ref(localStorage.getItem('rewardsActiveTab') || 'all'); // 'active' или 'all'
const unlinking = ref(false); // Флаг процесса отвязки Twitter-аккаунта

// Состояние для истории наград
const rewardHistory = ref([]);
const loadingHistory = ref(false);

// Состояние для топ-3 пользователей
const topUsers = ref([]);
const loadingTopUsers = ref(false);

// Состояние для отслеживания переключения табов
const tabSwitching = ref(false);

// Состояния для управления тостами
const showToast = ref(false);
const toastMessage = ref('');
const toastType = ref('info'); // 'success', 'error', 'warning', 'info'

// Функция для переключения между Twitter и Telegram
const setActiveNetwork = (network) => {
  activeNetwork.value = network;
  localStorage.setItem('rewardsActiveNetwork', network);
  
  // Если переключаемся на Telegram, загружаем статистику
  if (network === 'telegram' && isAuthenticated.value) {
    fetchTelegramStats();
  }
};

// Получение всех постов для наград с пагинацией
const fetchActivePosts = async (page = 1) => {
  try {
    loading.value = true;
    // Добавляем параметр activeOnly в зависимости от выбранного таба
    const activeOnly = activeTab.value === 'active';
    const response = await axios.get(`/rewards/active-posts?page=${page}&limit=${postsPerPage.value}&activeOnly=${activeOnly}`);
    if (response.data.success) {
      activePosts.value = response.data.posts;
      totalPages.value = response.data.pagination.totalPages;
      totalPosts.value = response.data.pagination.total;
      currentPage.value = response.data.pagination.currentPage;
    }
  } catch (error) {
    console.error('Error fetching active posts:', error);
    activePosts.value = [];
    showToastMessage('Error loading posts: ' + (error.response?.data?.message || error.message), 'error');
  } finally {
    loading.value = false;
  }
};

// Изменение страницы пагинации
const changePage = (page) => {
  if (page < 1 || page > totalPages.value) return;
  currentPage.value = page;
  fetchActivePosts(page);
};

// Переключение между табами активных и всех постов
const switchTab = (tab) => {
  // Показываем индикатор загрузки
  tabSwitching.value = true;
  
  // Сохраняем выбранный таб
  activeTab.value = tab;
  localStorage.setItem('rewardsActiveTab', tab);
  
  // Добавляем небольшую задержку для плавности
  setTimeout(() => {
    if (tab === 'history') {
      fetchRewardHistory();
    } else {
      fetchActivePosts();
    }
    
    // Скрываем индикатор загрузки через небольшое время
    setTimeout(() => {
      tabSwitching.value = false;
    }, 100);
  }, 100);
};

// Функция для получения истории наград
const fetchRewardHistory = async () => {
  if (!isAuthenticated.value) return;
  
  loadingHistory.value = true;
  
  try {
    const response = await axios.get('/rewards/history');
    if (response.data.success) {
      rewardHistory.value = response.data.history;
    } else {
      showToastMessage('Failed to load rewards history', 'error');
    }
  } catch (error) {
    console.error('Error fetching rewards history:', error);
    showToastMessage('Failed to load rewards history', 'error');
  } finally {
    loadingHistory.value = false;
  }
};

// Функция для получения топ-3 пользователей
const fetchTopUsers = async () => {
  loadingTopUsers.value = true;
  
  try {
    const response = await axios.get('/rewards/top3-users');
    if (response.data.success) {
      topUsers.value = response.data.users;
    } else {
      console.error('Failed to load top users');
    }
  } catch (error) {
    console.error('Error fetching top users:', error);
  } finally {
    loadingTopUsers.value = false;
  }
};

// Функция для получения статистики Telegram
const fetchTelegramStats = async () => {
  if (!isAuthenticated.value) return;
  
  try {
    const response = await axios.get('/rewards/tg-stats');
    if (response.data.success && response.data.stats) {
      // Обновляем только нужные поля
      telegramStats.totalMessages = response.data.stats.stats.totalMessages || 0;
      telegramStats.todayMessages = response.data.stats.stats.todayMessages || 0;
      telegramStats.weeklyMessages = response.data.stats.stats.weeklyMessages || 0;
      telegramStats.activeDays = response.data.stats.stats.activeDays || 0;
    } else {
      console.error('Failed to load Telegram stats');
    }
  } catch (error) {
    console.error('Error fetching Telegram stats:', error);
  }
};

// Функция для обрезки длинных имен пользователей
const truncateUserTag = (userTag) => {
  const maxLength = 7;
  if (userTag.length <= maxLength) {
    return userTag;
  }
  return userTag.substring(0, maxLength) + '...';
};

// Обработка изменения статуса привязки Twitter
const handleTwitterStatusChange = (isLinked) => {
  // Обновляем статус пользователя и список постов
  fetchUserStatus();
  fetchActivePosts(currentPage.value);
  
  // Показываем уведомление о статусе привязки
  if (isLinked) {
    showToastMessage('Twitter account successfully linked', 'success');
  }
};

// Показать тост с сообщением
const showToastMessage = (message, type = 'info') => {
  toastMessage.value = message;
  toastType.value = type;
  showToast.value = true;
};

// Закрыть тост
const closeToast = () => {
  showToast.value = false;
};

// Отвязка Twitter-аккаунта
const unlinkTwitterAccount = async () => {
  try {
    unlinking.value = true;
    
    const response = await axios.get('/twitter/unlink-username');
    
    if (response.data.success) {
      // Обработка изменения статуса Twitter
      userStatus.value.isTwitterLinked = false;
      userStatus.value.twitterUsername = '';
      fetchUserStatus(); // Обновляем статус пользователя
      
      // Показываем уведомление об успешной отвязке
      showToastMessage('Twitter account successfully unlinked', 'success');
    } else {
      showToastMessage(response.data.message || 'Error unlinking Twitter account', 'error');
    }
  } catch (error) {
    console.error('Error unlinking Twitter account:', error);
    showToastMessage(error.response?.data?.message || 'Error unlinking Twitter account', 'error');
  } finally {
    unlinking.value = false;
  }
};

// Получение статуса пользователя
const fetchUserStatus = async () => {
  if (!isAuthenticated.value) return;
  
  try {
    const response = await axios.get('/rewards/user-status');
    if (response.data.success) {
      userStatus.value = response.data;
    }
  } catch (error) {
    console.error('Error fetching user status:', error);
    showToastMessage('Error loading user status: ' + (error.response?.data?.message || error.message), 'error');
  }
};

// Обработка голосования за пост
const handleVote = async (postId, approve) => {
  try {
    const response = await axios.post(`/rewards/vote/${postId}`, { approve });
    
    if (response.data.success) {
      showToastMessage('Vote recorded successfully', 'success');
      fetchActivePosts(); // Обновляем список постов
    } else {
      showToastMessage(response.data.message || 'Error recording vote', 'error');
    }
  } catch (error) {
    console.error('Error voting for post:', error);
    showToastMessage('Error voting for post', 'error');
  }
};

// Обработка изменения статуса Telegram
const handleTelegramStatusChange = (status) => {
  isTelegramLinked.value = status.isLinked;
  telegramUsername.value = status.telegramUsername || '';
  fetchUserStatus(); // Обновляем статус пользователя
};

// Обработка завершения поста
const handleEndPost = async (postId) => {
  try {
    const response = await axios.post('/rewards/end-post', {
      postId
    });
    
    if (response.data.success) {
      // Обновляем список постов
      fetchActivePosts();
    }
  } catch (error) {
    console.error('Error ending post:', error);
  }
};

// Обработчик успешной привязки Twitter-аккаунта
const handleTwitterLinked = () => {
  // Обновляем статус пользователя
  fetchUserStatus();
};

onMounted(() => {
  fetchTopUsers();
  fetchUserStatus();
  
  // Загружаем статистику Telegram, если активна вкладка Telegram
  if (activeNetwork.value === 'telegram' && isAuthenticated.value) {
    fetchTelegramStats();
  }
  
  if (activeTab.value === 'history') {
    fetchRewardHistory();
  } else {
    fetchActivePosts();
  }
});
</script>

