<template>
  <div class="reward-post-card">
    <Toast 
      :message="toastMessage" 
      :type="toastType" 
      :show="showToast" 
      @close="closeToast"
    />
    <!-- Статус поста -->
    <div class="post-status">
      <span v-if="!post.approved" class="status-badge rejected">
        <i class="fas fa-ban"></i> Rejected
      </span>
      <span v-else-if="isInactive" class="status-badge inactive">
        <i class="fas fa-clock"></i> Inactive
      </span>
      <span v-else-if="isActive" class="status-badge active">
        <i class="fas fa-check"></i> Active
      </span>
    </div>
    
    <div class="post-header">
      <div class="post-author">
        <i class="fas fa-user-circle"></i>
        <router-link v-if="post.userTag !== 'system'" :to="`/player/${post.userTag}`" class="post-user">{{ post.userTag }}</router-link>
        <span class="system-badge" v-else>Official</span>
      </div>
      <div class="post-date">
        <i class="fas fa-calendar-alt"></i>
        <span v-if="isActive">{{ remainingTimeText }}</span>
        <span v-else>{{ formatDate(post.createdAt) }}</span>
      </div>
    </div>
    
    <div class="post-content">
      <div class="tweet-embed">
        <!-- Для отклоненных постов не показываем ссылку -->
        <a v-if="post.approved" :href="`https://twitter.com/i/status/${post.tweetId}`" target="_blank" class="tweet-link">
          <i class="fab fa-twitter"></i>
          View Tweet {{ post.tweetId }}
        </a>
        <div v-else class="blocked-tweet">
          <i class="fas fa-lock"></i>
          Post has been blocked due to community moderation
        </div>
        <div v-if="post.approved && !isActive" class="tweet-stats">
          <div class="stat-item">
            <i class="fas fa-heart"></i>
            <span>{{ post.usersLiked.length }} likes</span>
          </div>
          <div class="stat-item">
            <i class="fas fa-retweet"></i>
            <span>{{ post.usersRetweeted.length }} retweets</span>
          </div>
        </div>

        <div v-if="post.approved && !isActive" class="tweet-stats">
          <p class="tweet-stats-text">Interactions verified, rewards awarded</p>
        </div>

        <div v-if="post.approved && isActive" class="tweet-stats">
          <p class="tweet-stats-text">You can interact with this tweet. After the post is finished, we will check the results and reward you</p>
        </div>
        <router-link :to="`/rewards/tweet/${post.tweetId}`" class="view-details-link">
          <i class="fas fa-external-link-alt"></i> View Details
        </router-link>
      </div>
      
      <div class="reward-info">
        <h3>Reward Info</h3>
        <p>Interact with this tweet to earn:</p>
        <ul>
          <li><i class="fas fa-heart"></i> Like: +1 power</li>
          <li><i class="fas fa-retweet"></i> Retweet: +3 power</li>
          <li v-if="post.userTag !== 'system'"><i class="fas fa-vote-yea"></i> Vote: +1 power</li>
        </ul>
      </div>
    </div>
    
    <div class="post-actions" v-if="isAuthenticated">
      <!-- Для отклоненных постов показываем сообщение о завершении голосования -->
      <div class="voting-closed" v-if="!post.approved">
        <div class="rejection-message">
          <p><i class="fas fa-exclamation-triangle"></i> This post has been rejected by community moderation.</p>
          <p>Rewards were awarded to those who voted to reject the post.</p>
        </div>
      </div>
      
      <!-- Для одобренных постов показываем кнопки голосования или результаты -->
      <template v-else>
        <!-- Для активных постов показываем кнопки голосования -->
        <div class="vote-actions" v-if="post.userTag !== 'system' && isActive">
          <button @click="vote(true)" class="approve-button" :class="{ 'active': hasVotedApprove }">
            <i class="fas fa-thumbs-up"></i> Approve
          </button>
          <button @click="vote(false)" class="reject-button" :class="{ 'active': hasVotedReject }">
            <i class="fas fa-thumbs-down"></i> Reject
          </button>
        </div>
        
        <!-- Для неактивных постов показываем сообщение о завершении -->
        <div class="voting-ended" v-if="isInactive && post.approved && post.userTag !== 'system'">
          <div class="ended-message">
            <i class="fas fa-clock"></i>
            <p>This post has ended and voting is closed.</p>
          </div>
        </div>
        
        <button 
          v-if="canEndPost" 
          @click="endPost" 
          class="end-button"
        >
          <i class="fas fa-times-circle"></i> End Reward
        </button>
      </template>
    </div>
    
    <div class="post-footer">
      <div v-if="post.userTag !== 'system'" class="approval-stats">
        <div class="approval-bar">
          <!-- Зеленая полоса (за) -->
          <div 
            class="approval-progress approval-progress--approve" 
            :style="{ width: `${approvedPercentage}%` }"
          ></div>
          <!-- Красная полоса (против) -->
          <div 
            class="approval-progress approval-progress--reject" 
            :style="{ width: `${rejectedPercentage}%`, right: 0 }"
          ></div>
        </div>
        <div class="approval-text">
          <span>{{ post.usersApproved.length }} approved</span>
          <span>{{ post.usersRejected.length }} rejected</span>
        </div>
      </div>
      <div v-else class="approval-stats">
        <div class="approval-text">
          <span>This post is created by the system and cannot be voted on</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed, ref, onMounted, onUnmounted } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import Toast from '@/components/Toast.vue';

const props = defineProps({
  post: {
    type: Object,
    required: true
  }
});

const emit = defineEmits(['vote', 'end']);

const store = useStore();
const isAuthenticated = computed(() => store.getters.isAuthenticated);
const userTag = computed(() => store.getters.userTag);

// Проверка, голосовал ли пользователь за этот пост
const hasVoted = computed(() => {
  if (!userTag.value) return false;
  return props.post.usersApproved.includes(userTag.value) || props.post.usersRejected.includes(userTag.value);
});

// Проверка, голосовал ли пользователь за одобрение
const hasVotedApprove = computed(() => {
  if (!userTag.value) return false;
  return props.post.usersApproved.includes(userTag.value);
});

// Проверка, голосовал ли пользователь за отклонение
const hasVotedReject = computed(() => {
  if (!userTag.value) return false;
  return props.post.usersRejected.includes(userTag.value);
});

// Проверка, активен ли пост
const isActive = computed(() => {
  if (!props.post.endedTime || props.post.checkTime) return false;
  
  const now = new Date();
  const endTime = new Date(props.post.endedTime);
  
  // Пост активен, если время окончания еще не наступило и он не проверен
  return endTime > now && !props.post.checkTime;
});

const isInactive = computed(() => {
  // Пост неактивен, если он не активен
  return !isActive.value;
});

// Расчет оставшегося времени до конца активного поста
const remainingTime = ref(0);
const remainingTimeText = ref('');

// Обновляем оставшееся время каждую минуту
const updateRemainingTime = () => {
  if (!isActive.value || !props.post.endedTime) return;
  
  const now = new Date();
  const endTime = new Date(props.post.endedTime);
  const diffMs = endTime - now;
  
  if (diffMs <= 0) {
    remainingTime.value = 0;
    remainingTimeText.value = 'Ended';
    return;
  }
  
  // Переводим разницу в минуты
  const diffMinutes = Math.floor(diffMs / 60000);
  const hours = Math.floor(diffMinutes / 60);
  const minutes = diffMinutes % 60;
  
  remainingTime.value = diffMinutes;
  
  if (hours > 0) {
    remainingTimeText.value = `${hours}h ${minutes}m left`;
  } else {
    remainingTimeText.value = `${minutes}m left`;
  }
};

// Запускаем таймер при создании компонента
let timer = null;

// Состояния для управления тостами
const showToast = ref(false);
const toastMessage = ref('');
const toastType = ref('info'); // 'success', 'error', 'warning', 'info'

onMounted(() => {
  updateRemainingTime();
  timer = setInterval(updateRemainingTime, 60000); // Обновляем каждую минуту
});

onUnmounted(() => {
  if (timer) clearInterval(timer);
});

// Форматирование даты
const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString('en-US', { 
    year: 'numeric', 
    month: 'short', 
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
};

// Расчет процентов одобрения и отклонения
const approvedCount = computed(() => props.post.usersApproved.length);
const rejectedCount = computed(() => props.post.usersRejected.length);
const totalVotes = computed(() => approvedCount.value + rejectedCount.value);

// Процент одобрения (для совместимости со старым кодом)
const approvalPercentage = computed(() => {
  if (totalVotes.value === 0) return 50; // Default to 50% if no votes
  return Math.round((approvedCount.value / totalVotes.value) * 100);
});

// Процент голосов "за" для отображения на баре
const approvedPercentage = computed(() => {
  if (totalVotes.value === 0) return 50; // Если нет голосов, показываем 50/50
  return Math.round((approvedCount.value / (totalVotes.value || 1)) * 100);
});

// Процент голосов "против" для отображения на баре
const rejectedPercentage = computed(() => {
  if (totalVotes.value === 0) return 50; // Если нет голосов, показываем 50/50
  return Math.round((rejectedCount.value / (totalVotes.value || 1)) * 100);
});

// Check if user can end the post
const canEndPost = computed(() => {
  if (!isAuthenticated.value || !userTag.value) return false;
  
  // User can end post if they are the author or admin
  // props.post.userTag === userTag.value || 
  return userTag.value === 'roman';
});

// Показать тост с сообщением
const showToastMessage = (message, type = 'info') => {
  toastMessage.value = message;
  toastType.value = type;
  showToast.value = true;
};

// Закрыть тост
const closeToast = () => {
  showToast.value = false;
};

// Голосование за пост
const vote = async (approve) => {
  try {
    const response = await axios.post('/rewards/vote', {
      postId: props.post.id,
      approve
    });
    
    if (response.data.success) {
      // Обновляем только текущий пост
      const updatedPostResponse = await axios.get(`/rewards/post/${props.post.tweetId}`);
      if (updatedPostResponse.data.success) {
        // Обновляем данные поста
        Object.assign(props.post, updatedPostResponse.data.post);
        // Показываем уведомление об успешном голосовании
        showToastMessage(response.data.message, 'success');
      }
    }
  } catch (error) {
    console.error('Error voting for post:', error);
    showToastMessage(error.response?.data?.message || 'Error voting for post', 'error');
  }
};

// End post reward
const endPost = () => {
  emit('end', props.post.id);
};
</script>

