<template>
  <div class="telegram-link-form">
    <div v-if="loading" class="loading">
      <i class="fas fa-spinner fa-spin"></i> Checking Telegram status...
    </div>
    <div v-else>
      <div v-if="isLinked" class="telegram-linked-container">
        <div class="telegram-account-card">
          <div class="telegram-account-info">
            <i class="fab fa-telegram telegram-icon"></i>
            <div class="account-details">
              <h3>Linked Telegram Account</h3>
              <p class="telegram-username">@{{ telegramUsername }}
                <span class="verification-status" :class="{ 'verified': verified }">
                  {{ verified ? 'Verified' : 'Not Verified' }}
                </span>
              </p>
            </div>
          </div>
          <button @click="unlinkTelegramAccount" class="unlink-button" :disabled="unlinking">
            <i class="fas fa-unlink" v-if="!unlinking"></i>
            <i class="fas fa-spinner fa-spin" v-else></i>
            {{ unlinking ? 'Unlinking...' : 'Unlink Account' }}
          </button>
        </div>
        
        <!-- Секция с инструкциями по верификации, если аккаунт не верифицирован -->
        <div v-if="!verified && verifyCode" class="verification-section">
          <h3>Verify Your Telegram Account</h3>
          <p>Please follow these steps:</p>
          <div class="steps">
            <span>1. Open one of our groups: <a href="https://t.me/SolanaBitcat" target="_blank">EN group</a> or <a href="https://t.me/BitCatCN_CTO" target="_blank">CN group</a></span>
            <span>2. Send the following verification code to the chat</span>
            <span>3. Click refresh button <button class="refresh-button" @click="checkTelegramLink">Refresh</button></span>
          </div>
          <div class="verify-code">{{ verifyCode }}</div>
          <p>Once verified, you'll be able to receive rewards for Telegram activity.</p>
        </div>
      </div>
      <form v-else @submit.prevent="linkTelegramAccount" class="link-form">
        <div class="form-group">
          <label for="telegramUsername">Telegram Username <small>(case sensitive)</small></label>
          <div class="input-wrapper">
            <span class="input-prefix">@</span>
            <input 
              type="text" 
              id="telegramUsername" 
              v-model="telegramUsername" 
              placeholder="Enter your Telegram username"
              required
            />
          </div>
          <button type="submit" class="submit-button" :disabled="linking">
            <i class="fas fa-link" v-if="!linking"></i>
            <i class="fas fa-spinner fa-spin" v-else></i>
            {{ linking ? 'Linking...' : 'Link Telegram Account' }}
          </button>
        </div>
        <div v-if="error" class="error-message form-error">
          <i class="fas fa-exclamation-circle"></i> {{ error }}
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';

export default {
  name: 'TelegramLinkForm',
  emits: ['status-changed'],
  setup(props, { emit }) {
    const store = useStore();
    const telegramUsername = ref('');
    const isLinked = ref(false);
    const verified = ref(false);
    const verifyCode = ref(null);
    const loading = ref(true);
    const error = ref(null);
    const linking = ref(false);
    const unlinking = ref(false);

    // Проверка статуса привязки Telegram
    const checkTelegramLink = async () => {
      loading.value = true;
      error.value = null;
      
      try {
        const response = await axios.get('/telegram/check');
        
        if (response.data.success) {
          isLinked.value = response.data.isLinked;
          if (response.data.isLinked) {
            telegramUsername.value = response.data.telegramUsername || '';
            verified.value = response.data.verified || false;
            verifyCode.value = response.data.verifyCode || null;
          } else {
            telegramUsername.value = '';
            verified.value = false;
            verifyCode.value = null;
          }
          
          emit('status-changed', {
            isLinked: isLinked.value,
            telegramUsername: telegramUsername.value,
            verified: verified.value
          });
        }
      } catch (err) {
        console.error('Error checking Telegram link:', err);
        error.value = 'Failed to check Telegram link status';
      } finally {
        loading.value = false;
      }
    };

    // Привязка Telegram аккаунта
    const linkTelegramAccount = async () => {
      if (!telegramUsername.value) return;
      
      linking.value = true;
      error.value = null;
      
      try {
        const response = await axios.post('/telegram/link', 
          { telegramUsername: telegramUsername.value }
        );
        
        if (response.data.success) {
          isLinked.value = true;
          telegramUsername.value = response.data.data.telegramUsername;
          verified.value = response.data.data.verified;
          verifyCode.value = response.data.data.verifyCode;
          
          emit('status-changed', {
            isLinked: true,
            telegramUsername: telegramUsername.value,
            verified: verified.value
          });
        } else {
          error.value = response.data.message || 'Failed to link Telegram account';
        }
      } catch (err) {
        console.error('Error linking Telegram account:', err);
        error.value = 'Error linking Telegram account';
      } finally {
        linking.value = false;
      }
    };

    // Отвязка Telegram аккаунта
    const unlinkTelegramAccount = async () => {
      unlinking.value = true;
      error.value = null;
      
      try {
        const response = await axios.delete('/telegram/unlink');
        
        if (response.data.success) {
          isLinked.value = false;
          telegramUsername.value = '';
          verified.value = false;
          verifyCode.value = null;
          
          emit('status-changed', {
            isLinked: false,
            telegramUsername: '',
            verified: false
          });
        } else {
          error.value = response.data.message || 'Failed to unlink Telegram account';
        }
      } catch (err) {
        console.error('Error unlinking Telegram account:', err);
        error.value = 'Error unlinking Telegram account';
      } finally {
        unlinking.value = false;
      }
    };

    // Загрузка данных при монтировании компонента
    onMounted(() => {
      checkTelegramLink();
    });

    return {
      telegramUsername,
      isLinked,
      verified,
      verifyCode,
      loading,
      error,
      linking,
      unlinking,
      linkTelegramAccount,
      unlinkTelegramAccount,
      checkTelegramLink
    };
  }
};
</script>

